@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cookie&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

.App {
  text-align: center;
}

h1 {
  font-family: "Cookie", cursive;
  font-weight: bolder;
  font-size: 72px;
  background: -webkit-linear-gradient(#3eadcf, #7acdce);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h2 {
  font-family: "Cookie", cursive;
  color: #fff;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 28px;
}

h4 {
  font-family: "Helvetica Neue", sans-serif;
  font-weight: 600;
  font-size: 32px;
  color: #000000;
}

h5 {
  font-size: 16px;
  color: #000;
}

h6 {
  margin: 20px 0;
  line-height: 1.25;
}

p {
  font-family: "Montserrat", sans-serif;
}

a{
  text-decoration: none;
}

/*------------------------------------------- Topbar Styling ------------------------------------------*/
.topbar {
  background: rgba(0, 0, 0, 0.8);
}

.topbar-sub-button {
  font-size: 25px;
  color: #fff;
}

/* Banner Styling */
.banner{
  margin-top: 45px;
  padding: 90px 0;
}

.banner{
  height: 500px;
  display: flex;
  align-items: center;
}

.banner-text, .banner-img{
  height: 350px;
}

.banner-text {
  background: #fff;
  padding-top: 60px;
}

.banner-img img{
  height: 300px;
}

/* Banner\Get Slot Button */
.bookSlot {
  padding: 10px 15px;
  border: none;
  background: #3eadcf;
  color: #fff;
}

.bookSlot:hover {
  background-color: #1ea7d1;
  box-shadow: 0 4px 4px 2px rgba(0, 0, 0, 0.2), 0 6px 12px 0 rgba(0, 0, 0, 0.19);
}

/* Main Container[Calender, Sidepanel] */
.main-container {
  /* margin-top: 40px; */
  background-image: linear-gradient(315deg, #3eadcf, #abe9cd);
  background-size: cover;
  padding: 100px 0;
}

/*------------------------------------------ Sidepanel Styling ----------------------------------------*/

.available-slots {
  margin: 12px;
  font-size: 14px;
  padding: 14px 40px;
  border: 1px solid #fff;
  background: #fff;
  color: #3eadcf;
  transition: 0.25s;
}

.selected-slot{
  background-image: linear-gradient(295deg, #3eadcf, #7acdce);
  color: #fff;
}

.available-slots:hover {
  background-image: linear-gradient(295deg, #3eadcf, #7acdce);
  color: #fff;
  border: 1px solid #fff;
  padding: 14px 40px;
  box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 12px 0 rgba(0, 0, 0, 0.19);
}

.proceed {
  font-size: 14px;
  padding: 10px 15px;
  border: 1px solid #fff;
  color: #fff;
  background: #3eadcf;
}

.disable{
  font-size: 14px;
  padding: 10px 15px;
  border: 1px solid #fff;
  color: #fff;
  background: #777;
}

.slot-proceed {
  width: 120px;
}

.slot-proceed:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.App-link {
  color: #61dafb;
}

/*------------------------------------------- Calender Styling ------------------------------------------*/
.calendar {
  margin: 0 auto;
  margin-right: 25px;
  width: 600px;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.month {
  width: 100%;
  padding: 40px 30px;
  text-align: center;
  background-color: #fff;
  color: #fff;
}

#month {
  font-size: 42px;
  margin: 0;
  font-family: "Cookie", cursive;
  background: -webkit-linear-gradient(#3eadcf, #7acdce);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 500;
}

.weekdays {
  background-color: #3eadcf;
  color: #fff;
  padding: 7px 0;
  display: flex;
}

.days {
  background-color: #fff;
  font-weight: 300;
  padding: 10px 0;
  display: flex;
  flex-wrap: wrap;
}

.weekdays div,
.days div {
  text-align: center;
  width: 14.28%;
}

.days CalenderCell {
  text-align: center;
  width: 14.28%;
}

.days div {
  padding: 10px 0;
  margin-bottom: 10px;
  transition: all 0.4s;
}

.prev_date {
  color: #999;
}

.today {
  border: 2px solid #3eadcf;
}

.selected-date {
  background-color: #3eadcf;
  color: #fff;
}

.disabled{
  color: #dfe6e9
}

.days div:hover {
  cursor: pointer;
  background-color: #6bbdd6;
  color: #fff;
}

.prev,
.next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 23px;
  background-color: #3eadcf;
  transition: all 0.4s;
}

.prev:hover,
.next:hover {
  cursor: pointer;
}

/*Form Styling*/

.btn-close{
  position: absolute;
  top: 3px;
  right: 2px;
}

.appointment-form div{
  position: relative;
  margin-top: 15px;
  border-bottom: 1px solid #818181;
}

.appointment-form input {
  width: 80%;
  font-family: "Open Sans", sans-serif;
  font-size: 17px;
  border: none;
  outline: none;
  margin-bottom: 5px;

}

.appointment-form textarea {
  border: none;
  outline: none;
}

.form-icon{
  position: absolute;
  right: 5px;
  height: 30px;
  color: #818181;
}

.ta-form-icon{
  height: 100px;
}

.form-footer {

  display: flex;
  justify-content: center;
}

/*--------------------------------------- Youtube Container Styling --------------------------------------*/
.promotion {
  background-color: #f5f5f7;
  padding: 20px 15px;
}

.promotion .promo-card {
  padding: 50px 30px;
  background-color: #fff;
  border-radius: 30px;
  box-shadow: 0 12px 8px 0 rgba(236, 236, 236, 0.1), 0 6px 20px 0 rgba(108, 122, 236, 0.1);
  transition: padding;
}

.promo-card:hover{
  cursor: pointer;
}

.promotion .buy-api{
  background-image: linear-gradient(225deg,#bbe3f0 , #bfffe2);
  color: #fff;
  padding: 80px 50px;
  margin: 0 10px;
  transition: 0.3s;
  -webkit-backdrop-filter: saturate(180%) blur(20px)
}

.buy-api h4{
  color: #000000;
}

.promotion .plugsBlack {
  width: 400px;
  margin: 20px;
}

.promotion .email{
  width: 110px;
}

.promotion .contact-me{
  padding: 50px 40px;
  transition: 0.3s;
}

.contact-me h5{
  text-align: left;
}

.contact-me h4{
  text-align: left;
  font-size: 28px;
}
.promotion .yt-container {
  background-color: #212121;
  color: #fff;
  transition: 0.3s;
}

.yt-container h3{
  font-size: 20px;
}

.yt-container p{
  font-size: 14px;
}

.yt-container span {
  font-weight: 800;
}

.yt-subs {
  font-size: 12px;
  border: none;
  padding: 4px;
  background-color: rgb(245, 19, 19);
  color: #fff;
}

.yt-subs a {
  color: #fff;
  text-decoration: none;
}

.buy-api:hover ,.yt-container:hover, .contact-me:hover{
  transform: scale(1.05);
}

/* --------------------------------------------- Footer Styling --------------------------------------*/
.footer {
  padding: 38px 30px 10px 30px;
  text-align: left;
  background-color: #f5f5f7;
}

.footer h3 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 24px;
  font-weight: bolder;
  color: #535151;
}

.footer-brandName {
  text-align: left;
}

.footer-logo {
  width: 50px;
}

.footer p {
  font-size: 14px;
  font-weight: 500;
}

.footer-subscribe {
  width: 200px;
}

.social p {
  font-size: 12px;
}

.social span {
  font-weight: 700;
  color: #535151;
}

.social-icons {
  padding: 8px;
  color: #000;
  border-radius: 50%;
}

.social-icons:hover {
  color: #1ea7d1;
}

.paymentSuccess span {
  color: #1ed14b;
}

@media (max-width: 768px) {
  .yt-container-text {
    max-width: 80%;
    margin: auto;
  }
}

